* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.Body {
   background: linear-gradient(to bottom,rgb(81, 2, 81),white);
  height: 240vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
}

/*HOME*/

#webdiv{
  height: 100vh;
  width: 100vw;
  background-image: url('./images/nature2.jpg');
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 100px;
}
.webheading{
  margin-top: -5rem;
  color: rgb(253, 253, 253);
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif ;
  letter-spacing: 5px;
  font-size: xxx-large;
  filter: drop-shadow(4px 4px 3px rgb(235, 47, 79))
}
.webbtn{
  height: 90px;
  width: 300px;
  border: 4px ridge rgb(135, 139, 5);
  background-color: rgb(153, 161, 2);
  color: wheat;
  box-shadow: 0 0 20px white;
  margin-inline: auto;
  transition: background-color .3s ease-in ,transform .6s ease;
}

.webbtn:hover{
 background-color: rgb(132, 132, 4);
 transform: scale(1.1);
}

/*BLACK HOLE*/

.blackhole {
   height: 80px;
  width: 200px;
  filter: brightness(5);
}
.InnerBody {
  height: 400px;
  width: 700px;
  position: relative;
  background-color: rgba(16, 16, 16, 0.995);
  top: 6em;
  box-shadow: 0px 0px 5px rgb(39, 39, 39);
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: center;
  flex-direction: column;
  z-index: 99999;
}
.form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 150px;
}

.logo {
  margin-top: 120px;
  height: 0.2em;
  width: 600px;
  opacity: 0.4;
}
.logoimg {
  object-fit: cover;
  height: inherit;
  width: inherit;
}
.txt {
   height: 70px;
  width: 550px;
  filter: drop-shadow(0 0 0.6em rgba(16, 16, 16, 0.626));
  border-radius: 2em;
  font-family:'Courier New', Courier, monospace;
  font-weight: 600;
  font-size: medium;
  text-indent: 20px;
  letter-spacing: 2px;
  border: 2px solid whitesmoke;
  z-index: 9999;
}
.txt:focus {
  outline: 4px solid rgb(118, 2, 118);
}
.btn {
 margin-top: 20px;
  height: 60px;
  width: 400px;
  border-radius: 5px;
  background-color: rgb(95, 7, 95);
  color: #fefefeda;
  font-family: fantasy;
  font-size: larger;
  font-weight: bold;
  border: 2px solid black;
}
.btn,.webbtn:hover {
  cursor: pointer;
}

.innerbody2 {
  border-radius: 5px;
  height: 120vh;
  position: relative;
  width: 83%;
  margin-top: 100px;
  margin-bottom: 50px;
  overflow-y: scroll;
  overflow-x: none;
  scroll-behavior: smooth;
  background-color: rgba(250, 247, 247, 0.242);
   scrollbar-width: none;
}
.showdata {
  height: fit-content;
  width:fit-content;
  padding: 70px;
  
}

#titless {
  font-weight: 800;
  font-size: x-large;
  color: rgba(165, 6, 6, 0.963);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.link {
  margin-top: 6px;
  font-family:Arial, Helvetica, sans-serif,fantasy;
  font-size: large;
  text-decoration: none;
  color: rgba(4, 4, 196, 0.74);
}

.desc {
  margin-top: 4px;
  color: rgb(37, 37, 37);
  font-family: Arial, Helvetica, sans-serif;
  font-size: medium;
}
h3 {
  font-weight: bold;
  font-size: x-large;
}


.section {
  
  gap: 9px;
  padding: 5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: start;
  border-bottom: 1px solid rgba(0, 0, 0, 0.413);
  flex-direction: column;
}

.section:hover {
  cursor: pointer;
}

/* FOOTERRRRRRRRR */

.footer {
  background-color: rgba(14, 13, 13, 0.9);
  display: flex;
  padding: 20px;
  margin-top: 15px;
  justify-content: space-evenly;
  height: 180px;
  width: 100vw;
}
.foot1 {
  justify-content:space-evenly;
  width: 30vw;
  display: flex;
  flex-direction: column;
}
li {
  font: bold;
  font-family: "Times New Roman", Times, serif;
  font-size: medium;
  color: white;
  list-style: none;
}
h3 {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: medium;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.854);
}
#copyright {
  color: white;
  font-weight: bolder;
  font-size: larger;
}

#titles {
  font-size: x-large;
  font-weight: bold;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
.github > a{
  color: white;
  text-decoration: none;
  font-size: medium;
}
.github > a:hover{
  color: lightpink;
}
.github>img{
  filter: invert(5);
}
.github{
display: flex;
gap: 10px;
align-items: center;
}

@media only screen and (max-width:480px) {

  .InnerBody{
    width: 330px;
  }
  .Body{
    width: 100vw;
    height: 170vh;
  }
  .txt{
    width: 300px;
    font-weight: 400;
  }
  .btn{
    width: 200px;
  }
  .innerbody2{
    height: 30rem;
    margin: 0;
    width: 21rem;
  }
  .showdata{
    width: fit-content;
    padding: 0;
  }
  .section{
    padding: 10px;
    width: inherit;
    display: flex;
    flex-wrap: wrap;
  }
  #titless{
   font-size: medium;
   width: fit-content;
   font-weight: bolder;
   text-wrap: wrap;
  }
  .link{
    font-size: small;

  }
  .logo{
    width: 20rem;
    margin: 0;
  }
  .desc{
    font-size: small;
  }
  .footer{
    flex-direction: column;
    gap: 20px;
    height: 210px;
    width: 100vw;
    padding-top: 20px;
    margin: 0;
  }
  #copyright{
    font-size: medium;
  }
  .foot1{
    list-style: none;
    height: 120px;
    width: fit-content;
    display: flex;
  
  }
  .webbtn{
    width: 240px;
  }
 
  #webdiv{
    gap: 40px;
  }
  .webheading{
    font-size:xx-large;
    line-height: 44px;
    text-align: center;
  }
}